body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.App {
  text-align: center;
}

.App-header {
  background-color: #222;
  height: 10vh;
  padding: 10vh;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

@media screen and (min-width: 1280px) {
  .imageGallery {
    display: grid;
    padding: 0;
    grid-template-columns: 30vw 30vw 30vw;
    justify-content: center;
    grid-auto-rows: 50vh;
    grid-gap: 2vw;
    list-style: none inside none;
  }
}

@media screen and (max-width: 800px) {
  .imageGallery {
    display: grid;
    padding: 0;
    grid-template-columns: 95vw;
    justify-content: center;
    grid-gap: 2vw;
    list-style: none inside none;
  }
}

.aGalleryPhoto {
  object-fit: contain;
  width: 100%;
  height: 100%;
}

